import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Col, Row, Card, Form, Input, Spin, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { SelectAjax } from 'components/common/index';
import { getTrainTypes } from 'services/trainType';
import {
  createEnrollStudyField,
  updateEnrollStudyField,
  getEnrollStudyFieldById,
} from 'services/enrollStudyField';
import { getQueryParams } from 'utils/urlUtil';

// const { Option } = Select;

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    id: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormBillPage' })
class FormPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
    loading: false,
  };

  componentDidMount = () => {
    const { id: queryParamId } = getQueryParams();
    if (queryParamId) {
      this.setState({ dataId: queryParamId });
      this.getDataById(queryParamId);
    }
  };

  getDataById = async id => {
    const res = await getEnrollStudyFieldById(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  saveData = async values => {
    const { dataId } = this.state;
    this.setState({ loading: true });
    try {
      if (!dataId) {
        await createEnrollStudyField(values);
      } else {
        await updateEnrollStudyField({ id: dataId, ...values });
      }
      this.props.history.goBack();
    } catch (error) {
      message.error('C li xy ra');
    }
    this.setState({ loading: false });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId, loading } = this.state;
    return (
      <Spin spinning={loading}>
        <Card title={`${dataId ? 'Cp nht' : 'Thm'} ha n`}>
          <Row>
            <Form onSubmit={this.handleSubmit}>
              <Row>
                <Col span={24}>
                  <Form.Item hasFeedback label="M ngnh" {...formItemLayout}>
                    {getFieldDecorator('id', {
                      initialValue: initialValue.id,
                      rules: [
                        {
                          required: true,
                          message: 'Vui lng nhp thng tin!',
                        },
                      ],
                    })(<Input disabled={!!dataId} />)}
                  </Form.Item>
                </Col>
              </Row>
              <Row>
                <Col span={24}>
                  <Form.Item hasFeedback label="Tn ngnh" {...formItemLayout}>
                    {getFieldDecorator('label', {
                      initialValue: initialValue.label,
                      rules: [
                        {
                          required: true,
                          message: 'Vui lng nhp thng tin!',
                        },
                      ],
                    })(<Input />)}
                  </Form.Item>
                </Col>
              </Row>
              <Row>
                <Col span={24}>
                  <Form.Item hasFeedback label="H o to" {...formItemLayout}>
                    {getFieldDecorator('trainTypeId', {
                      initialValue: initialValue.trainTypeId,
                      rules: [
                        {
                          required: true,
                          message: 'Vui lng nhp thng tin!',
                        },
                      ],
                    })(<SelectAjax service={getTrainTypes} />)}
                  </Form.Item>
                </Col>
              </Row>
              <Form.Item {...formItemLayout} label=" ">
                <BtnSave htmlType="submit" />
                <BtnCancel onClick={() => this.props.history.goBack()} />
              </Form.Item>
            </Form>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormPage {...props} />
  </Page>
));



